.grid-container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    gap: 16px;
    background-color: #fef7e5;
  }
  .grid-item {
    padding: 20px;
    text-align: center;
    background-color: rgb(255, 255, 255);
    justify-content: center;
    align-items: center;
    display: flex;
  }